'use client';

import Image from 'next/image';
import { useState } from 'react';
import { useRouter } from 'next/navigation';

export default function UserAvatar() {
  const router = useRouter();
  const [isLoggedIn] = useState(false);
  const [showDropdown, setShowDropdown] = useState(false);

  const handleClick = () => {
    if (isLoggedIn) {
      router.push('/user');
    } else {
      router.push('/login');
    }
  };

  return (
    <div className="fixed top-4 right-4 z-[9999]">
      <div 
        className="relative w-10 h-10 rounded-full overflow-hidden border-2 border-white shadow-lg cursor-pointer"
        onClick={handleClick}
        onMouseEnter={() => setShowDropdown(true)}
        onMouseLeave={() => setShowDropdown(false)}
      >
        <Image
          src="https://file.psd.cn/p/stock/20221014/ccemnih3yxg.jpg"
          alt="用户头像"
          fill
          className="object-cover"
        />
      </div>
      {showDropdown && (
        <div className="absolute right-0 mt-2 w-48 bg-white/10 backdrop-blur-sm rounded-lg shadow-lg p-2">
          {isLoggedIn ? (
            <div className="text-white">
              <div className="font-medium">用户名</div>
              <div className="text-sm text-gray-300">点击查看个人信息</div>
            </div>
          ) : (
            <div className="text-white">
              <div className="text-sm text-gray-300">点击登录</div>
            </div>
          )}
        </div>
      )}
    </div>
  );
} 