'use client';

import VideoPlayer from '@/components/VideoPlayer';
import UserAvatar from '@/components/UserAvatar';
import EpisodeList from '@/components/EpisodeList';
import { useSearchParams } from 'next/navigation';
import { useEffect, useState } from 'react';

// Define the type for video data
type VideoData = {
  title: string;
  url: string;
  cover: string;
};

// Function to fetch video data from the API
async function fetchVideoData(id: string, ep: string): Promise<{ title: string; url: string; cover: string }> {
  try {
    const response = await fetch(`/api/videos/${id}/episodes/${ep}`);
    if (!response.ok) {
      throw new Error('网络响应失败');
    }
    const data = await response.json();
    console.log('Fetched data:', data);
    return data;
  } catch (error) {
    console.error('Error fetching video data:', error);
    throw error;
  }
}

// Main component for the video page
export default function VideoPage() {
  const searchParams = useSearchParams();

  // 检查 searchParams 是否为 null
  const id = searchParams ? searchParams.get('id') : null;
  const ep = searchParams ? searchParams.get('ep') : null;

  // State to hold video data
  const [videoData, setVideoData] = useState<VideoData | null>(null);

  // useEffect to fetch video data when id or ep change
  useEffect(() => {
    if (id && ep) {
      fetchVideoData(id, ep).then(data => {
        console.log('Setting videoData:', data);
        setVideoData(data);
      }).catch(error => {
        console.error('Error setting videoData:', error);
      });
    }
  }, [id, ep]);

  // If videoData is not yet available, show loading message
  if (!videoData) {
    return <div>加载中...</div>;
  }

  // Render the video player and other components
  return (
    <div className="fixed inset-0 bg-black overflow-hidden">
      <div className="absolute inset-0">
        <VideoPlayer
          url={videoData.url}
          title={videoData.title}
          cover={videoData.cover}
        />
      </div>
      <UserAvatar />
      <EpisodeList />
      <style jsx>{`
         .issue-button-class {
          display: none !important;
        }
      `}</style>
    </div>
  );
}
