'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';

interface Episode {
  id: string;
  title: string;
  isLocked: boolean;
}

export default function EpisodeList() {
  const router = useRouter();
  const [isExpanded, setIsExpanded] = useState(false);
  const [episodes] = useState<Episode[]>(Array.from({ length: 8 }, (_, i) => ({
    id: (i + 1).toString(),
    title: `第${i + 1}集`,
    isLocked: i >= 3, // 假设前三集是解锁的
  })));

  const handleEpisodeClick = (episode: Episode) => {
    if (episode.isLocked) {
      router.push(`/payment?episodeId=${episode.id}`);
    } else {
      // TODO: 切换到对应剧集
      console.log('切换到剧集:', episode.id);
    }
  };

  return (
    <div className="fixed right-4 top-1/2 -translate-y-1/2 z-[9999]">
      <button
        onClick={() => setIsExpanded(!isExpanded)}
        className="w-10 h-10 rounded-full bg-white/10 backdrop-blur-sm flex items-center justify-center text-white hover:bg-white/20 transition-colors"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M4 6h16M4 12h16M4 18h16"
          />
        </svg>
      </button>

      {isExpanded && (
        <div className="absolute right-0 mt-2 w-64 bg-white/10 backdrop-blur-sm rounded-lg shadow-lg p-4 max-h-64 overflow-y-auto">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-bold text-white">选集列表</h2>
            <button
              onClick={() => setIsExpanded(false)}
              className="text-white hover:text-gray-300"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                  clipRule="evenodd"
                />
              </svg>
            </button>
          </div>
          <div className="grid grid-cols-2 gap-2">
            {episodes.map((episode) => (
              <button
                key={episode.id}
                onClick={() => handleEpisodeClick(episode)}
                className={`p-2 rounded text-center transition-colors ${
                  episode.isLocked
                    ? 'bg-gray-800 text-gray-400 hover:bg-gray-700'
                    : 'bg-white/10 text-white hover:bg-white/20'
                }`}
              >
                <div className="relative">
                  {episode.title}
                  {episode.isLocked && (
                    <span className="absolute -top-1 -right-1 text-xs bg-yellow-500 text-black px-1 rounded">
                      锁定
                    </span>
                  )}
                </div>
              </button>
            ))}
          </div>
        </div>
      )}
    </div>
  );
} 