'use client';

import { useEffect, useRef } from 'react';
import type DPlayer from 'dplayer';
import 'dplayer/dist/DPlayer.min.css';

interface VideoPlayerProps {
  url: string;
  title?: string;
  cover?: string;
}

export default function VideoPlayer({ url, title, cover }: VideoPlayerProps) {
  const containerRef = useRef<HTMLDivElement>(null);
  const playerRef = useRef<DPlayer | null>(null);

  useEffect(() => {
    import('dplayer').then((DPlayerModule) => {
      if (containerRef.current && !playerRef.current) {
        const DPlayer = DPlayerModule.default;
        playerRef.current = new DPlayer({
          container: containerRef.current,
          video: {
            url: url,
            cover: cover,
            type: 'auto',
          },
          title: title,
          theme: '#FADFA3',
          autoplay: false,
          preload: 'auto',
          volume: 0.7,
          playbackSpeed: [0.5, 0.75, 1, 1.25, 1.5, 2],
          hotkey: true,
          logo: '/logo.png',
          subtitle: {
            url: '',
            type: 'webvtt',
            fontSize: '25px',
            bottom: '10%',
            color: '#fff',
          },
        });
      }
    });

    return () => {
      if (playerRef.current) {
        playerRef.current.destroy();
        playerRef.current = null;
      }
    };
  }, [url, title, cover]);

  return (
    <div className="w-screen h-screen">
      <div ref={containerRef} className="w-full h-full"></div>
    </div>
  );
} 